"use client";

import { useState } from "react";
import ModuleGrid from "@/components/marketplace/ModuleGrid";
import MarketplaceFilters from "@/components/marketplace/MarketplaceFilters";

export default function MarketplacePage() {
  const [filter, setFilter] = useState("all");

  return (
    <div className="max-w-7xl mx-auto px-6 py-12 space-y-10">
      {/* Header */}
      <header className="space-y-3">
        <h1 className="text-5xl font-semibold tracking-tight text white">
          Marketplace de Módulos
        </h1>
        <p className="text-neutral-400 max-w-2xl">
          Amplía tu sistema con módulos inteligentes diseñados para automatizar,
          escalar y profesionalizar tu negocio.
        </p>
      </header>

      {/* Filters */}
      <MarketplaceFilters value={filter} onChange={setFilter} />

      {/* Grid */}
      <ModuleGrid filter={filter} />
    </div>
  );
}
