"use client";

const modules = [
  {
    id: 1,
    name: "Autoblogging IA",
    description: "Publicación automática optimizada para SEO.",
    type: "pro",
    active: true,
  },
  {
    id: 2,
    name: "Tendencias",
    description: "Detecta oportunidades y tendencias del mercado.",
    type: "free",
    active: true,
  },
  {
    id: 3,
    name: "Proveedores",
    description: "Gestión inteligente de proveedores.",
    type: "pro",
    active: false,
  },
  {
    id: 4,
    name: "Shop AI",
    description: "Automatiza ventas y recomendaciones.",
    type: "pro",
    active: true,
  },
];

export default function ModuleGrid({ filter }) {
  const filteredModules = modules.filter((module) => {
    if (filter === "all") return true;
    if (filter === "active") return module.active;
    if (filter === "free") return module.type === "free";
    if (filter === "pro") return module.type === "pro";
    return true;
  });

  return (
    <div className="grid gap-6 sm:grid-cols-2 lg:grid-cols-3">
      {filteredModules.map((module) => (
        <div
          key={module.id}
          className="rounded-xl border border-neutral-800 bg-neutral-900/60 p-6 flex flex-col justify-between hover:border-neutral-700 transition"
        >
          <div className="space-y-3">
            <div className="flex items-center justify-between">
              <h3 className="text-lg font-medium text-white">
                {module.name}
              </h3>

              <span
                className={`text-xs px-2 py-1 rounded-full ${
                  module.type === "pro"
                    ? "bg-indigo-600/20 text-indigo-400"
                    : "bg-emerald-600/20 text-emerald-400"
                }`}
              >
                {module.type.toUpperCase()}
              </span>
            </div>

            <p className="text-sm text-neutral-400">
              {module.description}
            </p>
          </div>

          <div className="pt-6 flex items-center justify-between">
            <span
              className={`text-xs ${
                module.active
                  ? "text-emerald-400"
                  : "text-neutral-500"
              }`}
            >
              {module.active ? "Activo" : "Inactivo"}
            </span>

            <button className="text-sm font-medium text-indigo-400 hover:text-indigo-300">
              Ver módulo →
            </button>
          </div>
        </div>
      ))}
    </div>
  );
}
